import React from 'react'
import {
  Button,
  Box,
  Text,
  Modal,
  ModalOverlay,
  ModalContent,
  ModalHeader,
  ModalCloseButton,
  ModalBody,
  ModalFooter,
  useDisclosure,
  Divider,
  UnorderedList,
  ListItem,
  OrderedList,
  Accordion,
  AccordionItem,
  AccordionButton,
  AccordionPanel,
  AccordionIcon,
  List,
} from '@chakra-ui/react'
import { ChevronUpIcon, InfoOutlineIcon } from '@chakra-ui/icons'
import './App.css'

export const TitleOption = ({}) => {
  const { isOpen, onOpen, onClose } = useDisclosure()
  const scrollToTop = () => {
    window.scrollTo({
      top: 0,
      behavior: 'smooth',
    })
  }
  return (
    <Box position="relative">
      {document.documentElement.scrollTop > 100 ? (
        <Box bottom={3} right={3} position="fixed">
          <Button
            boxShadow={'dark-lg'}
            colorScheme="whiteAlpha"
            borderRadius={'sm'}
            variant="solid"
            w={'60px'}
            h="60px"
            onClick={scrollToTop}
          >
            <ChevronUpIcon boxSize={'3em'} />
          </Button>
        </Box>
      ) : (
        <></>
      )}
      <Box top={3} right={3} position="fixed">
        <Button
          boxShadow={'dark-lg'}
          colorScheme="blackAlpha"
          borderRadius={'sm'}
          variant="solid"
          w={'60px'}
          h="60px"
          onClick={onOpen}
        >
          <InfoOutlineIcon boxSize={'1.5em'} />
        </Button>
      </Box>
      <Modal isOpen={isOpen} onClose={onClose} size="2xl">
        <ModalOverlay />
        <ModalContent color={'white'} bg="gray.700">
          <ModalHeader m={'auto'}>TUTORIAL</ModalHeader>
          <Divider orientation="horizontal" m={'auto'} />
          <ModalCloseButton />
          <ModalBody m={'auto'}>
            <OrderedList>
              <ListItem></ListItem>
              <ListItem></ListItem>
              <ListItem>
                
              </ListItem>
              <ListItem>
                *PC//
              </ListItem>
              <ListItem>
                9
              </ListItem>
            </OrderedList>
            <UnorderedList mt={2} mb="6">
              <ListItem>Practice</ListItem>
              <ListItem>
                Easy3
              </ListItem>
              <ListItem>Hard</ListItem>
            </UnorderedList>
          </ModalBody>

          <ModalHeader m={'auto'}>UPDATE HISTORY</ModalHeader>
          <Accordion>
            <AccordionItem>
              <h2>
                <AccordionButton>
                  <Box flex="1" textAlign="left" fontSize={'lg'}>
                    Ver.0.2_coming soon
                  </Box>
                  <AccordionIcon />
                </AccordionButton>
              </h2>
              <AccordionPanel pb={4}>
                Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do
                eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut
                enim ad minim veniam, quis nostrud exercitation ullamco laboris
                nisi ut aliquip ex ea commodo consequat.
              </AccordionPanel>
            </AccordionItem>

            <AccordionItem>
              <h2>
                <AccordionButton>
                  <Box flex="1" textAlign="left" fontSize={'lg'}>
                    Ver.0.1_2022/8/31
                  </Box>
                  <AccordionIcon />
                </AccordionButton>
              </h2>
              <AccordionPanel pb={4}>
                <UnorderedList>
                  <ListItem></ListItem>
                  <ListItem>Practice/Easy/Hard</ListItem>
                  <ListItem></ListItem>
                  <ListItem></ListItem>
                  <ListItem></ListItem>
                </UnorderedList>
              </AccordionPanel>
            </AccordionItem>
          </Accordion>

          <ModalFooter>
            <Button
              colorScheme={'orange'}
              variant="outline"
              onClick={() => {
                document.location.reload(true)
              }}
            >
              Reload App
            </Button>
            <Button colorScheme="orange" ml={3} mr={3} onClick={onClose}>
              Close
            </Button>
          </ModalFooter>
        </ModalContent>
      </Modal>
    </Box>
  )
}
